import isEqual from "lodash/isEqual"
import {
  SourceSelectionInitialStateFullV2,
  SourceSelectorSelectedCategoryInfo,
  SourceSelectorSelectedSourceInfoV2,
} from "../sourceSelector.types"

type GetIsSourceSelectorPristineProps = {
  initialStateFull: SourceSelectionInitialStateFullV2 | null
  isFilterInclude: boolean
  selectedSources: Array<SourceSelectorSelectedSourceInfoV2>
  selectedCategories: Array<SourceSelectorSelectedCategoryInfo>
}

function getIsSourceSelectorPristineV2({
  initialStateFull,
  isFilterInclude,
  selectedSources,
  selectedCategories,
}: GetIsSourceSelectorPristineProps): boolean {
  return (
    !!initialStateFull &&
    isFilterInclude === initialStateFull.isFilterInclude &&
    isEqual(
      selectedSources.map(selectedSource => selectedSource.id),
      initialStateFull.sources.map(source => source.id),
    ) &&
    isEqual(
      selectedCategories.map(selectedCategory => selectedCategory.id),
      initialStateFull.categories.map(category => category.id),
    )
  )
}

export { getIsSourceSelectorPristineV2 }
